<template>
    <view>
        <view class="section-total">
            <view class="item">
                <text class="row1">累计收益(元)</text>
                <text class="row2">{{ balance }}</text>
            </view>
        </view>

        <view class="profit-list" v-if="isshow == 1">
            <view class="p-title">收益明细</view>
            <view class="list" v-for="(item, index) in list" :key="item.unique">
                <view class="col-1">{{ item.addtime }}</view>

                <view class="col-2" v-if="item.type == 1">
                    {{ item.remarks }}
                    <text class="color">+{{ item.num }}</text>
                    元({{ item.cash_status_name }})
                </view>

                <view class="col-2" v-if="item.type == 2">
                    {{ item.remarks }}
                    <text class="color">-{{ item.num }}</text>
                    元({{ item.cash_status_name }})
                </view>
            </view>
        </view>

        <view class="section-btns" v-if="isshow == 1">
            <view class="btn" @tap.stop.prevent="saveto">申请提现</view>
        </view>
    </view>
</template>

<script>
const app = getApp();

const md5 = require('../../utils/md5.js');

const common = require('../../utils/common.js');

export default {
    data() {
        return {
            balance: '0.00',
            list: [],
            isshow: 0
        };
    },
    onLoad: function () {
        uni.navloading();
        const that = this;
        let newcommon = new common();
        newcommon.checklogin();
        let param = newcommon.data;
        param.userid = uni.getStorageSync('userid');
        param.url = newcommon.apiurl + '?action=car.cashlog';
        newcommon.ajax(
            param,
            function (result) {
                let datainfo = result.data;

                if (datainfo.data.status == 1000) {
                    that.setData({
                        list: datainfo.data.data,
                        balance: datainfo.data.price_num,
                        isshow: 1
                    });
                } else if (datainfo.data.status == 1001) {
                    that.setData({
                        isshow: 0
                    });
                } else {
                    uni.showModal({
                        title: datainfo.data.info,
                        content: datainfo.data.info,
                        showCancel: false,
                        confirmText: '我知道了',
                        success: function (result1) {
                            if (result1.confirm) {
                                uni.switchTab({
                                    url: '../index/index'
                                });
                            }
                        }
                    });
                }

                console.log(that.isshow);
            },
            function () {
                uni.showModal({
                    title: '获取数据失败',
                    content: '服务器异常',
                    showCancel: false,
                    confirmText: '我知道了',
                    success: function (result1) {
                        if (result1.confirm) {
                            uni.navigateBack();
                        }
                    }
                });
            },
            function () {
                uni.navloading('close');
            }
        );
    },
    methods: {
        saveto: function () {
            uni.navigateTo({
                url: '../cashOut/cashOut'
            });
        }
    }
};
</script>
